import React, {FC} from 'react'
import {
    Button,
    Categories,
    CategoriesLine,
    GreenButton,
    MarginBlock,
    PurpleButton,
    RedButton,
    YellowButton
} from './style'

///////////// types for props
type PropsType = {
    SetShowSecondCategories: () => void
    SelectAllMainCategories: () => void
    ChangeMainCategory: (categoryId: number) => void
    
    showSecondCategories: boolean
    mainCategoriesState: Array<boolean>
    local: Array<string>
}


let ChooseCategory: FC<PropsType> = ({
                                         showSecondCategories, 
                                         SetShowSecondCategories,
                                         mainCategoriesState,
                                         SelectAllMainCategories,
                                         ChangeMainCategory,
                                         local
}) => {
    
    
     
    return <Categories>
            {/*@ts-ignore*/}
        <CategoriesLine active={true}>
            {/*@ts-ignore*/}
            <Button onClick={()=>{SelectAllMainCategories()}} active={mainCategoriesState[0]}>{local[0]}</Button>
            {/*@ts-ignore*/}
            <YellowButton onClick={()=>{ChangeMainCategory(1)}} active={mainCategoriesState[1]}>{local[1]}</YellowButton>
            {/*@ts-ignore*/}
            <GreenButton onClick={()=>{ChangeMainCategory(2)}} active={mainCategoriesState[2]}>{local[2]}</GreenButton>
            {/*@ts-ignore*/}
            <PurpleButton onClick={()=>{ChangeMainCategory(3)}} active={mainCategoriesState[3]}>{local[3]}</PurpleButton>
            {/*@ts-ignore*/}
            <RedButton onClick={()=>{ChangeMainCategory(4)}} active={mainCategoriesState[4]}>{local[4]}</RedButton>
            <MarginBlock><p/></MarginBlock>
        </CategoriesLine>
            {/*@ts-ignore*/}
        <CategoriesLine active={showSecondCategories}>
            {/*@ts-ignore*/}
            <Button onClick={()=>{SetShowSecondCategories()}} active={true}>{local[0]}</Button>
            {/*@ts-ignore*/}
            <Button active={false}>{local[5]}</Button>
            {/*@ts-ignore*/}
            <Button active={false}>{local[6]}</Button>
            {/*@ts-ignore*/}
            <Button active={false}>{local[7]}</Button>
            {/*@ts-ignore*/}
            <Button active={false}>{local[8]}</Button>
            <MarginBlock><p/></MarginBlock>
        </CategoriesLine>
    </Categories>
}


export default ChooseCategory