import React, {FC} from 'react'
import {AppStateType} from '../../../store/store'
import {connect, ConnectedProps} from 'react-redux'
import ChooseCategory from './choose-category'
import {ChangeMainCategory, SelectAllMainCategories, SetShowSecondCategories} from '../../../store/categories-reducer'

type PropsType = ConnectedProps<typeof connector>

let ChooseCategoryContainer: FC<PropsType> = ({
                                                  showSecondCategories,
                                                  SetShowSecondCategories,
                                                  mainCategoriesState,
                                                  SelectAllMainCategories,
                                                  ChangeMainCategory,
                                                  local
                                              }) => {
    
    let PropsCategories = {
        showSecondCategories,
        SetShowSecondCategories,
        mainCategoriesState,
        SelectAllMainCategories,
        ChangeMainCategory,
        local
    }
    
        return <ChooseCategory {...PropsCategories} />
    
}

///////////// create props for App component
let mapStateToProps = (state: AppStateType) => {
    return {
        local: state.LocalizationReducer.langData.categories,
        showSecondCategories: state.CategoriesReducer.showSecondCategories,
        mainCategoriesState: state.CategoriesReducer.mainCategoriesState
    }
}

const connector = connect(mapStateToProps, {
    SetShowSecondCategories,
    SelectAllMainCategories,
    ChangeMainCategory
})
export default connector(ChooseCategoryContainer)
